import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Button, Container, Row, Table } from 'react-bootstrap'
import { message } from 'antd'
import ReactPaginate from 'react-paginate'
import limit from '../../components/Constansts'
import StatusBadge from '../../components/StatusBadge'

const AddWalletReqHistory = () => {
  const currentPage = useRef(1)
  const [walletRequestHistory, setWalletRequestHistory] = useState([])
  const [pageCountHistory, setPageCountHistory] = useState(1)

  const getHistoryPaginatedData = async (page) => {
    try {
      const res = await axios.get(
        `/api/v1/masterAdmin/getWalletAddReqHistory?page=${currentPage.current}&limit=${limit}`,
      )
      if (res.data.success) {
        const data = res.data.data
        if (data.results.length === 0) {
          setWalletRequestHistory([])
        } else {
          setWalletRequestHistory(data.results)
          setPageCountHistory(data.totalPages) // Use total pages from the response
        }
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }

  const handlePageClickHistory = (e) => {
    const selectedPage = e.selected + 1
    currentPage.current = selectedPage
    getHistoryPaginatedData(selectedPage) // Fetch data for the selected page
  }
  useEffect(() => {
    currentPage.current = 1
    getHistoryPaginatedData()
  }, [])

  const formattedDate = (date) => {
    const dateObject = new Date(date)
    const format = `${dateObject.getFullYear()}-${String(dateObject.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(dateObject.getDate()).padStart(2, '0')} ${String(dateObject.getHours()).padStart(
      2,
      '0',
    )}:${String(dateObject.getMinutes()).padStart(2, '0')}:${String(
      dateObject.getSeconds(),
    ).padStart(2, '0')}`

    return format
  }

  return (
    <>
      {' '}
      <Row className="mb-3">
        <Container fluid>
          <h3>Add to Wallet Request History</h3>
          <Row>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">Sn</th>
                  <th className="text-center">Name</th>
                  <th className="text-center">UniqueId</th>
                  <th className="text-center">Transaction ID</th>
                  <th className="text-center">Amount</th>
                  <th className="text-center">Status</th>
                  <th className="text-center">Transfered On</th>
                </tr>
              </thead>
              <tbody>
                {walletRequestHistory && walletRequestHistory.length > 0 ? (
                  walletRequestHistory.map((item, index) => (
                    <tr key={index}>
                      <td className="text-center">{index + 1}</td>
                      <td className="text-center">{item.userId.name}</td>
                      <td className="text-center">{item.userId.uniqueId}</td>
                      <td className="text-center">{item.transactionId}</td>
                      <td className="text-center">{item.amount}</td>
                      <td className="text-center">{<StatusBadge status={item.status}/>}</td>
                      <td className="text-center">{formattedDate(item.date)}</td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="text-center text-danger">
                      No data found
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </Row>
        </Container>
      </Row>
      <Row>
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClickHistory}
          pageRangeDisplayed={5}
          pageCount={pageCountHistory}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          marginPagesDisplayed={2}
          containerClassName="pagination justify-content-center"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          activeClassName="active"
        />
      </Row>
    </>
  )
}

export default AddWalletReqHistory
